@import "sass-rem";

.mainSwiper {
  overflow: inherit;
  max-height: rem(500px);


}

.mainSlider {
  position: relative;
  box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.13);
  height: rem(398px);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: rem(33px);
  border-radius: rem(var(--sizeBg));

  @media(max-width: 991.98px) {
    height: rem(184px);
    &::before {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      content: "";
      border-radius: 16px;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.43) 0%, rgba(0, 0, 0, 0.43) 100%);
      z-index: 1;
      /* shadow-slider-333 */
    }
  }

  &__content {
    height: 100%;
    z-index: 1;
    max-width: rem(700px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    //gap: rem(16px);
    font-weight: 500;
    text-transform: capitalize;
    position: relative;
  }

  &__character {
    position: absolute;
    bottom: 0;
    right: 0;
    //max-width: 550px;
    max-height: rem(500px);
    @media (max-width: 991.98px) {
      height: rem(184px);
    }

    img {
      object-position: center;
      object-fit: contain;
      max-height: 100%;
    }
  }

  &__title {
    font-size: rem(28px);
    @media (max-width: 991.98px) {
      font-size: rem(12px);
    }
  }

  &__text {
    font-size: rem(33px);
    font-weight: 700;
    margin-bottom: rem(31px);
    @media(max-width: 991.98px) {
      font-size: rem(18px);
      margin-bottom: 0;
    }

    span {
      font-size: rem(53px);
      @media(max-width: 991.98px) {
        font-size: inherit;
      }
    }
  }

  &__subText {
    font-size: rem(18px);
    margin-bottom: rem(12px);
  }

  &__btn {
    width: fit-content;
    min-width: rem(230px);
    @media (max-width: 991.98px) {
      min-width: rem(198px);
      margin-top: rem(15px);
    }
  }
}